import vue from "vue";

//
window._EnumList_ = {};
//type
window._EnumCallbackFunc_ = {
  "*" : function(tp){return [];}
}

/*
* 
* import enums from './enums'
* Vue.use(enums);
*/
export function Enum() {}


//function 
/*
*Vue.prototype.$enum.callback(function(type){
*  return = Vue.prototype.$http.xpost("/dds/dictionary/get", { dic_type: "product_status" }, "", true) 
*})
*/
Enum.prototype.callback = function (callback, tp) {
  if (typeof callback == "function"){
    var type = tp || "*"
    window._EnumCallbackFunc_[type] = callback
    return
  }
  throw new Error(":function(type){return [];}");
};

//
Enum.prototype.set = function (data, type) {
  if (typeof data == "function"){
    if (!type){
        return
    }
    window._EnumCallbackFunc_[type] = data
    return
  }
  
  if (!Array.isArray(data) || data.length == 0) { 
    return
  }

  if(!checkData(data)){
    throw new Error(":[{type:'zxc',name:'',value:'1', pid: 1002},...]typepidnamevalue");
  }

  data.forEach(function (item) {
    var tp = item.type || type
    if(tp){
      if (!window._EnumList_[tp]) {
        window._EnumList_[tp] = [];
      }
      window._EnumList_[tp].push(item);
    }
  });
  return
};

//
Enum.prototype.get = function (type, pid) {
  if (!type) return [];

  var result = getEnumList(type, pid) //
  if (result.length == 0){
    result = getEnumListByCallback(type, pid)//
  }
  return result
};

//valuename
Enum.prototype.getName = function (type, value) {
  if (value == "") {
    return "-"
  }

  var enumMap = Enum.prototype.get(type)
  for (var i = 0; i < enumMap.length; i++){
    if (enumMap[i].value == value) {
      return enumMap[i].name
    }
  }
  return value 
}

//type
Enum.prototype.clear = function (type) {
  window._EnumList_[type] = null;
};

//filter
vue.filter('fltrEnum', (value, enumType) => {
  return Enum.prototype.getName(enumType, value)
})

//
function checkData(data){
  for (var i = 0; i < data.length; i++){
    if(!Object.prototype.hasOwnProperty.call(data[i], "name") || !Object.prototype.hasOwnProperty.call(data[i], "value")){
      return false
    }
  }
  return true
}

//
function getEnumListByCallback(type, pid){
  var handle = window._EnumCallbackFunc_[type] || window._EnumCallbackFunc_["*"]
  
  var data = handle.apply(this, [type])
  Enum.prototype.set(data, type)
  return getEnumList(type, pid)
}

//typewindow._EnumList_
function getEnumList(type, pid){
  var result = window._EnumList_[type] || []
  if (!pid){
    return result
  }
  var list = []
  result.forEach((item)=>{
    if (item.pid == pid){
      list.push(item)
    }
  })
  return list
}


